<template>
  <div>
    <div class="bg"><div class="im" id="bg"></div></div>
    <div class="back">
      <div class="content">
        <div id="top" class="ding"></div>
        <h1 class="topic">我们的项目和价值观</h1>
        <img src="../assets/in1-1.png" class="tu" alt="no pic" />
        <img src="../assets/in1-2.png" class="tu" alt="no pic" />
        <img src="../assets/in1-3.png" class="tu" alt="no pic" />
        <div class="ding"></div>
        <p class="pagraph">
          帕金森病 (Parkinson’s disease, PD)
          是一种高发于中老年神经系统的变性疾病,
          是继阿尔茨海默病之后的第二大常见神经退行性疾病。PD患者运动功能受到阻碍，且会进一步出现如自主功能不全、认知缺损等非运动特征的并发症，严重影响了患者的生活质量。我们试图通过改造腺相关病毒，从不同通路上多靶点治疗帕金森氏症，希望能为帕金森氏症甚至其他神经退行性疾病未来的治愈提供可能的解决方案。
        </p>
        <p class="pagraph">
          我们将责任和人性化定义为我们的核心价值观。人类实践团队在整个项目周期中不断强调我们核心价值观的实现。我们将通过调研问卷、走访调查等形式探讨项目的道德与伦理。我们联系了利益相关者，包括地方政府、临床医生、专家、商业人员，以确认我们正在开发一个对世界负责且有益的项目。通过他们的指导，我们能够评估我们项目设计的有效性、安全性、可行性，旨在为科学研究和医疗做出贡献。
        </p>


      <div id="des1" class="ding"></div>
        <h1 class="title">Storyline</h1>
        <p class="pagraph">问题理解->项目构思->实验设计->总体评估->应用实践</p>
        <p class="pagraph">
          由于Storyline有较强的时间逻辑（项目进行后具体产生了问题，才能进一步和专家讨论、解决），我们设想在真正项目实践后按照Storyline撰写Integrated
          Human Practices 的wiki。
        </p>
        <p class="pagraph">
          具体形式参考2021年KCL_UK Integrated Human Practices模块。
        </p>
        <p class="ref">
          https://2021.igem.org/Team:KCL_UK/Human_Practices#Human-centric-design
        </p>
        <img src="../assets/in1-2-1.png" class="tu" alt="no pic" />
        <img src="../assets/in1-2-1.png" class="tu" alt="no pic" />


<div id="des2" class="ding"></div>
        <h1 class="title">道德与伦理</h1>
        <p class="pagraph">
          我们设计了调研问卷并进行了一对一采访，在社会群体（分年龄段，18岁以下，18~30岁，30岁以上）中调研公众对病毒载体医疗以及基因治疗的了解度及接受程度。
        </p>
        <p class="pagraph">
          我们和专家会谈，向他们介绍我们的项目，并探讨基于腺相关病毒的基因治疗的安全性与道德伦理问题、相关病毒治疗产品的风险与周期。
        </p>
        <img src="../assets/in1-3-1.png" class="tu" alt="no pic" />


<div id="des3" class="ding"></div>
        <h1 class="title">利益相关者</h1>
        <div class="showBox">
          <div class="head">
            <div id="c1" class="c" @click="ch1()">患者</div>
            <div id="c2" class="c" @click="ch2()">医生</div>
            <div id="c3" class="c" @click="ch3()">政府与社区</div>
          </div>
          <div class="box">
            <div id="p1" class="p">
              <p class="pagraph">
                iGEM队员通过在浙二医院的见习，了解了帕金森患者的生活状态与关切问题。我们了解到早期帕金森病患者一般采取多巴胺类似药物左旋多巴进行治疗，而晚期患者一般采取手术电生理介入治疗。因此，我们针对不同发病阶段的患者开展两份问卷调查。
              </p>
              <h1 class="title3">早期帕金森病患者问卷设计</h1>
              <p class="pagraph">
                第一次确诊PD的时间（年龄段选择）
                用药过程中遇到的最棘手的问题：药品昂贵、定时服药麻烦、药效不良、不良反应
                对目前PD疗法的了解：药物疗法、手术疗法、基因疗法
              </p>
              <p class="pagraph">
                用药过程中遇到的最棘手的问题：药品昂贵、定时服药麻烦、药效不良、不良反应
                对目前PD疗法的了解：药物疗法、手术疗法、基因疗法
              </p>
              <h1 class="title3">晚期帕金森病患者问卷设计</h1>
              <p class="pagraph">第一次确诊PD的时间（年龄段选择）</p>
              <p class="pagraph">目前是否接受手术介入治疗</p>
              <p class="pagraph">
                用药过程中遇到的最棘手的问题：药品昂贵、定时服药麻烦、药效不良、不良反应
                手术过程中遇到的最棘手的问题：费用昂贵、不良反应、效果不佳
              </p>
              <img src="../assets/in1-4-1.png" class="tu" alt="no pic" />
              <p class="pagraph">
                在了解患者群体的过程中，我们特别关注患者及其家属在治疗过程中，出现的心理问题以及他们对于经济社会因素的考量，希望在医院得到何种非医疗资源的帮助，是否存在心理与人文关怀的特殊需求等等。我们希望通过调查与临床医生与院方交流，完善基于生物-心理-社会医学模式的治疗，让患者在治疗之外，感受到更多的人文关怀和情感支持。
              </p>
            </div>
            <div id="p2" class="p">
              <p class="pagraph">
                我们向内科和外科医生询问目前用药和手术层面对PD的治疗措施，了解在临床层面两种治疗方式的优势和各自有待解决的问题。
              </p>
              <p class="pagraph">
                在了解了临床方面的治疗措施后，我们向医生们介绍IGEM项目的产品，就给药方式、替代现有治疗措施的可能性与其进行探讨，挖掘我们产品可进一步提升的空间，以及基因治疗目前在临床的应用情况，目前遇到的瓶颈等等。
              </p>
              <p class="pagraph">
                除此之外，我们特别就PD患者在治疗过程中最大的心理问题以及经济社会因素的考量与临床医生开展探讨，针对已经开展的基因治疗询问目前患者的接受度以及目前临床的普及率。通过医生的介绍，我们对已经开展基因治疗的患者进行问卷调查，向其介绍我们的产品，询问其对我们产品的顾虑，以及其使用我们产品的意愿。
              </p>
            </div>
            <div id="p3" class="p">
              <p class="pagraph">
                由于老年帕金森患者占帕金森患者群体的大部分，我们拟通过杭州较具有影响力的临终关怀组织——晒福绿郡·沐椿生命关爱义工之家，开展生命关爱-认识帕金森的讲座与相关活动宣传，积累初期受众群体。在讲座前期通过问卷收集帕金森患者治疗过程中关心的问题并予以解答，同时向其详细介绍帕金森的致病机理以及可能的预防措施。在介绍了帕金森的现有疗法后，介绍TheraParkin项目，向观众普及基因治疗帕金森的可能实现路径。
              </p>
              <p class="pagraph">
                我们通过讲座进一步收集帕金森患者的问题，进行研究与解答。另外，通过沐椿生命关怀组织和在场观众，获得杭州地区帕金森患者家属组织的联系方式，与负责人取得联系。后续可通过线上线下等方式，邀请不同地区帕金森亲友会的组织负责人参与会议，收集了解帕金森病人以及家属关心的问题，并向其介绍TheraParkin项目，重点就项目的理论可行性与负责人进行探讨，收集负责人反馈的项目可行性问题。
              </p>

              <p class="pagraph">
                之后，在会议的基础上进一步推进，积极促成民间组织到官方政府的合作与宣传，通过各个负责人或杭州的生命关爱组织，联系对项目感兴趣的政府部门，开展TheraParkin介绍会和帕金森病交流会，通过撰文、视频以及义诊反馈增加政府对我们项目的兴趣。积极推进患者-组织负责人-政府三级宣传与实践模式的落地，团队成员希望成立TheraParkin了解平台，让更多的患者、家属以及对帕金森病与合成生物学疗法感兴趣的人，通过这个三级式的了解平台获得不同层次的治疗信息与情感帮助。
              </p>
              <img src="../assets/in1-4-2.png" class="tu" alt="no pic" />
            </div>
          </div>
        </div>

        

