import Vue from 'vue'
import VueRouter from 'vue-router'

import myHome from '@/components/myHome.vue'

import description from '@/components/description.vue'
import design from '@/components/design.vue'
import proof from '@/components/proof.vue'
import experiment from '@/components/experiment.vue'
import safety from '@/components/safety.vue'
import imple from '@/components/imple.vue'

import Integrated from '@/components/Integrated.vue'
import communication from '@/components/communication.vue'
import education from '@/components/education.vue'
import inclusivity from '@/components/inclusivity.vue'
import entre from '@/components/entre.vue'
import sustain from '@/components/sustain.vue'

import model from '@/components/model.vue'
import software from '@/components/software.vue'
import hardware from '@/components/hardware.vue'

import part from '@/components/part.vue'

Vue.use(VueRouter)
const router = new VueRouter({
    routes:[
        {path:"/",component:myHome},
        {path:"/myHome",component:myHome},
        {path:"/description",component:description},
        {path:"/design",component:design},
        {path:"/proof",component:proof},
        {path:"/experiment",component:experiment},
        {path:"/safety",component:safety},
        {path:"/imple",component:imple},

        {path:"/Integrated",component:Integrated},
        {path:"/communication",component:communication},
        {path:"/education",component:education},
        {path:"/inclusivity",component:inclusivity},
        {path:"/entre",component:entre},
        {path:"/sustain",component:sustain},

        {path:"/model",component:model},
        {path:"/software",component:software},
        {path:"/hardware",component:hardware},

        {path:"/part",component:part},


    ]
    
})
export default router