var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('div',{staticClass:"bg"},[_c('div',{staticClass:"im",attrs:{"id":"bg"}})]),_c('div',{staticClass:"back"},[_c('div',{staticClass:"content"},[_c('div',{staticClass:"ding",attrs:{"id":"top"}}),_c('h1',{staticClass:"topic"},[_vm._v("α-syn蛋白通路")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("作用机理")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 帕金森病的典型病例特征是中枢神经系统以异常α-突触核蛋白（α-synuclein，α-syn）不溶性聚集体为主要成分的路易小体（Lewy body， LB）的形成，导致多巴胺能神经元的进行性死亡和丢失。编码α-syn的SNCA基因是帕金森的关键致病基因之一。该基因的突变可引起α-syn 的过表达，进而形成路易小体，导致帕金森病的发生。因此，SNCA基因被认为是对帕金森进行治疗的有效靶点。 ")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/des1-1.png"),"alt":"no pic"}}),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title2"},[_vm._v("解决方案")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 结合α-syn聚集体的形成和其调控基因，我们设计了两条通路来抑制路易小体的形成：1）促进α-syn聚集体的解聚：分子伴侣介导的自噬被证明能够有效促进α-syn的降解，其中hsp70的高表达可以促进这一过程，缓解α-syn蛋白的聚集毒性（Witt, S. N. Hsp70 molecular chaperones and Parkinson’s disease. 2010，Biopolymers，93:218–228.）；2）减少α-syn的表达水平：通过调控SNCA基因的转录和翻译过程，可有效降低α-syn的表达水平，减少α-syn的积累和聚集体的形成。我们选择使用短发夹RNA（shRNA）来干扰SNCA的表达。ShRNA较普遍使用的siRNA具有更长的药效潜力。 ")]),_c('p',{staticClass:"ref"},[_vm._v(" ENG H, LIU N, YANG Y Y, et al. Lentivirus-mediated downregulation of α-synuclein reduces neuroinflammation and pro[1]motes functional recovery in rats with spinal cord injury.2019, Neuroinflammation, 16(1): 283. ")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/des1-2.png"),"alt":"no pic"}}),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/des1-2-2.png"),"alt":"no pic"}}),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title2"},[_vm._v("基因线路")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 在基因线路设计上，我们参考前人文章[1]设计了一个基于CRISPR/Cas9系统的与门电路，以保证我们的治疗只会在表达特定转录因子的靶细胞（大脑黑质多巴胺能神经元）中进行。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 该与门电路的初始表达由两个特异性表达在靶细胞中的转录因子PITX3和FOXA2驱动。当该电路进入靶细胞后，其中的特异性转录因子PITX3和FOXA2会驱动下游所有蛋白/RNA的少量表达，少量的Cas9/VP64会与sgRNA1一起，在VP64结构域激活下正反馈促进这两个物质自身的转录，同理，sgRNA2也能激活自身的转录。正反馈转录完成后，高浓度的Cas9/VP64会与sgRNA2一起，在Cas9结构域的催化下，敲除LacI基因，这样，由LacO启动子启动的shRNA就可被表达，沉默SNCA基因，降低α-syn蛋白的表达；sgRNA3也能与Cas9/VP64一起，在VP64结构域的激活下激活Hsp70，促进错误折叠α-syn蛋白重新折叠或诱导其降解。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 当该与门电路进入非靶细胞时，由于缺少Pitx3/Foxa2转录因子中的一个或两个，Cas9/VP64和sgRNA不能被同时表达，本底表达的LacI会抑制shRNA和sgRNA3的表达，最大程度减少外源基因的泄露表达和治疗的脱靶效应。 ")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/des1-3.png"),"alt":"no pic"}}),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title2"},[_vm._v("AAV. CAP. B10载体介绍")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 血脑屏障（BBB）是指脑毛细血管壁与神经胶质细胞形成的血浆与脑细胞之间的屏障，和由脉络丛形成的血浆和脑脊液之间的屏障，仅允许特定类型的分子从血流进入大脑神经元和其他周围细胞。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 血脑屏障的存在，对于阻止有害物质由血液进入大脑具有重要意义，然而，血脑屏障同时也阻止了绝大部分小分子和大分子（例如多肽，蛋白质和核酸）的转移，严重限制了神经中枢系统疾病（例如神经退行性疾病、脑肿瘤，脑部感染和中风等）的治疗。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 为了使我们的AAV能够突破血脑屏障的阻碍，我们选择了加州理工学院Viviana Gradinaru 团队通过定向进化开发的AAV.CAP-B10这一AAV变体。该载体能够通过血液注射突破血脑屏障，靶向神经细胞，同时不会在肝脏富集，避免了可能引起的肝脏毒性副作用。该载体目前不仅在小鼠上得到了证实，在非人灵长类动物狨猴上也得到了证实。同时，该载体在中脑神经元中的表达量是其他脑区的五倍多。这为我们利用AAV来治疗帕金森提供了一种更安全、更有效的治疗选择。 ")]),_c('p',{staticClass:"ref"},[_vm._v(" Goertsen, D., Flytzanis, N.C., Goeden, N. et al. AAV capsid variants with brain-wide transgene expression and decreased liver targeting after intravenous delivery in mouse and marmoset. 2021, Nat Neurosci 25:106–115. ")]),_c('div',{staticClass:"ding",attrs:{"id":"des1"}}),_c('h1',{staticClass:"topic"},[_vm._v(" PTB途径")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("作用机理")]),_c('p',{staticClass:"pagraph"},[_vm._v("通过替换丢失的多巴胺能神经元以重建中断的中脑-多巴胺能神经元回路是一种稳定长效的治疗方法。[1]有文献表明，RNA结合蛋白PTB及其神经元类似物nPTB在控制神经元诱导和成熟中具有重要作用，通过连续消耗PTB实现了星形胶质细胞向多巴胺能神经元的转化[2][3]。 ")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/des2-1.jpg"),"alt":"no pic"}}),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("解决方案")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 为了沉默PTB基因，我们选择了一种基于dCas9-DNMT3A的DNA甲基化修饰策略。与其他基因沉默的方法不同，DNA甲基化对基因表达的作用更稳定和长期。dCas9-DNMT3A系统由Vojta等开发，是由dCas9(核酸酶缺陷，不能切割DNA)与DNMT3A的催化结构域(一种能够在体内甲基化CpG位点的活性DNA甲基转移酶）融合，可以在gRNA的指导下仍能以相同的精确度靶向和结合DNA。 ")]),_c('p',{staticClass:"ref"},[_vm._v(" Vojta A,Dobrinié P, Tadic V,et al.Repurposing the CRISPR-Cas9 system for targeted DNA methylation Nucleic Acids Research, 2016,44 (12):5615-5628. ")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/des2-2.png"),"alt":"no pic"}}),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("基因线路")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 在具体的通路设计上，首先根据PTB基因，设计相应的sgRNA序列，以靶向目的基因。其次，运用星形胶质细胞的特异性启动子GFAP，控制dCas和DNMT3A融合蛋白的转录。最后加上合适的核定位序列（NLS）和转录增强子等腺相关病毒转导表达元件，完成载体设计。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ①通路：hU6-sgRNA-GFAP-dCas-DNMT3A-WPRE （hU6 一种人类非编码RNA启动子，GFAP星形胶质细胞特异性启动子，WPRE是腺相关病毒的转录增强子） ")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/des2-3-1.png"),"alt":"no pic"}}),_c('p',{staticClass:"pagraph"},[_vm._v(" 甲基化沉默基因作用原理 ")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/des2-3-2.png"),"alt":"no pic"}}),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("rAAV6载体介绍")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 神经胶质细胞，特别是星形胶质细胞，在利用AAV或rAAV载体进行感染时，速率非常低，且靶向性较弱。为了克服这一局限性，提高侵染效率，我们选择已商用的rAAV6载体。根据研究，市售的rAAV6在体外和体内均对大鼠星形胶质细胞具有高倾向性，并且可以相当有选择性地转导大鼠皮质细胞。在体内，rAAV6在受感染的大脑区域转导>90%的GFAP +星形胶质细胞，能够确保星形胶质细胞的高侵染率。 ")]),_c('div',{staticClass:"ding",attrs:{"id":"des2"}}),_c('h1',{staticClass:"topic"},[_vm._v("检测")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 通过检测多中心大样本队列，发现相对于健康人，帕金森病患者的脑脊液中含有α-syn的胞外囊泡会显著降低。使用纳米流式细胞检测技术及EVs内标记技术，一种稳定、快速的测定方法，用于定量测定脑脊液中含有α-syn的EVs；此创新性纳米流式细胞检测技术的方法不仅比传统的免疫测定法检测更具灵敏度、提高了检测效率，还为快速体液诊断及早期体液诊断提供了新的技术方法和新的标志物。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 创新性纳米流式细胞检测技术，是在流式细胞仪上对样本进行检测，相较于PET等影像学手段，花费低，能够给患者极大减轻经济负担。同时，该项新技术能够检测微囊泡内外的标记物，可达到多标记物快速联合检测的效果，若结合中枢神经系统来源标志物与疾病相关标志物，能够有效避免外周蛋白的干扰，精准检测神经系统来源携带有疾病相关蛋白的微囊泡。 ")])]),_c('div',{staticClass:"ding"}