        <div class="ding" id="top"></div>
        <h1 class="title">概述</h1>
        <p class="pagraph">
          在TheraParkin项目中，
          我们致力于打造包容的学习环境，让合成生物学社区更加开放。我们希望影响尽可能多的人，因此我们面向社会的不同群体开展了相关活动。在包容性模块，我们的主要焦点群体是一类特殊群体——帕金森患者，他们具有特殊的症状，我们致力于打造患者友好的创业与教育形式。
        </p>
        <p class="pagraph">
          另外，由于帕金森患者中老年人群体居多，我们专门开展了面向老年人的教育模式。目前人口老龄化程度不断加深，打造老年友好型的包容体系也是很有意义的。我们在教育和创业部分，从内容和形式两个方面构造了具有包容性和多元性的活动体系。
        </p>

        <div class="ding"></div>
        <h1 class="title">帕金森患者</h1>
        <p class="pagraph">
          2022年，我们针对帕金森患者群体，在不同的时间地点进行了宣传教育。在时间的选择上，我们将活动时间尽可能定在周末，同时在4月11日——帕金森日举办了大型的活动。在地点的选择上我们尽可能覆盖较多的群体，考虑到患者间的差异性，有的患者由于身体原因无法来到现场参与讲座，我们决定前往养老院，医院，甚至老人家中进行宣传教育。我们力求让每一位想了解帕金森治愈和生物学的患者都收获知识。
        </p>

        <div class="showBox">
          <div class="head">
            <div id="c1" class="c" @click="ch1()">社区讲座</div>
            <div id="c2" class="c" @click="ch2()">养老院会谈</div>
            <div id="c3" class="c" @click="ch3()">家中对话</div>
             <div id="c4" class="c" @click="ch4()">青年群体</div>
          </div>
          <div class="box">
            <div id="p1" class="p">
              <p class="pagraph">
我们请有能力来到社区会议现场的朋友们进入现场，佩戴口罩，隔位就坐。会议每隔一段时间，就请他们休息一下，在期间夹杂部分康复训练。康复训练包括头颈部的训练，手部训练和站立坐下训练，我们在讲台和人群中间进行相关演示，确保他们无论有没有基础都能学会这些练习。会议的最后我们送给他们大字版的宣传单，以及一些适合康复训练的道具。

              </p>
            </div>
            <div id="p2" class="p">
                <p class="pagraph">
                 部分患者由于身体原因可能不能来到会议现场，因此我们决定前往养老院进行交谈活动。在这里，我们印象颇深，他们表现出明显的震颤和行动迟缓的症状，这也增强了我们的使命感。我们以做志愿者护工的形式，小心搀扶着他们，并在交谈中讲述了我们的项目。我们亲切地了解每一位患者的情况，并且承诺下次再见。最后我们给他们分享了相关案例，让他们对康复充满希望。 
              </p>
            </div>
            <div id="p3" class="p">
                <p class="pagraph">
                  我们曾加入当地的帕友群进行活动的宣传。群友们都非常支持我们的活动。但由于时间、地点的安排，社区活动并不能兼顾到群内的每一位患者。此时有一位患者的儿子通过电话联系我们，他在群内看到了我们的消息，并表明他父亲非常希望能够参与此次活动，问是否能进行单独的知识传授。我们同意了这一请求。我们带着礼物来到这位老爷爷的家中，从他的口中我们了解到这么多年来他与帕金森之间的斗争，在谈话的间隙，他时不时地哽咽。我们感同身受，并表明我们项目的价值观念。在讲座的最后我们给他送了我们的手册和一面定制的小旗。
              </p>
            </div>
            <div id="p4" class="p">
                <p class="pagraph">
                  在帕金森患者群体中，还有少部分的青年人，他们中的部分人由于工作忙无法来到我们的活动现场。我们通过邮箱、短信、群聊等形式发送我们的介绍信息和项目wiki。同时附上了电子版的康复训练指南。
              </p>
              <p class="pagraph">
                  他们当中的一部分对我们的项目很感兴趣，并且希望得到更深一步的了解。我们考虑到这一点，对我们的项目wiki进行了高层次的设计，来方便患者人群的查看。我们改进了普通版本的wiki，加入了自动播放、大字版、简化版、夜间版等模式。这也是我们的包容性所在。
              </p>
            </div>
          </div>
