    <h1 class="title">老年群体</h1>
    <p class="pagraph">
我国65岁以上人群PD的患病率大约是1.7%，这是一个比较高的比例，因此我们希望向整个老年群体科普有关于帕金森和合成生物学的知识。我们联系了杭州市有名的临终关怀组织，在他们的帮助下我们进行了分批次的老年群体公众教育。对于项目介绍，我们用简化版、大字版的介绍为活动资料发放给大家，并且删除了其中晦涩难懂的词语。
    </p>
    <div class="ding" id="des3"></div>
