我们对帕金森患者的家属也开展了相关活动。重点在于心态把握和对患者的关怀，并且向他们表示关心。他们基于亲人的情况，对于帕金森知识抱有极大的好奇，我们向他们科普了帕金森与合成生物学的知识，帮助他们了解帕金森现状和未来的发展趋向。
    </p>
    <p class="pagraph">
另外，我们与部分家属讲述了我们的创业活动，他们十分期待我们的药物能够最终推广，并且有意向使用我们的药物产品。

    </p>
    <h3 class="title4">大众</h3>
    
     <p class="pagraph">
对于大众的科普，我们从帕金森的自测和预防，和心态处理两个方面进行介绍科普。从心态上把握，从生活的小事中做到。
    </p>

