子伴侣蛋白，在检测到蛋白质错误折叠后会辅助蛋白质重新折叠或将其送入蛋白降解途径进行降解。现有的研究表明，在表达有错误SCNA基因的细胞系中过表达Hsp70后，会显著降低α-syn聚集体在胞内的数量，证明该通路可以用于我们的治疗。 ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("实验操作")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ①构建m-α-syn:EGFP融合表达载体，在特定细胞系内进行表达，观察聚集体分布及数量。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ②在该细胞系中进一步过表达Hsp70，观察聚集体分布及数量。若聚集体减少则说明过表达Hsp70能促进突变型α-syn的减少。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ③在表达有m-α-syn的细胞系中进一步表达shRNA，检测SNCA的表达水平和蛋白翻译水平，并观察聚集体的分布及数量。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ④验证与门开关在细胞内的工作情况 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ⑤验证AAV-CAP-B10侵染细胞的情况以及最终能否实现目标 ")]),_c('div',{staticClass:"ding",attrs:{"id":"des3"}}),_c('h1',{staticClass:"topic"},[_vm._v("PTB通路的可行性")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("理论基础")]),_c('p',{staticClass:"pagraph"},[_vm._v(" PTB通路的实现是通过消耗PTB实现星形胶质细胞的转分化，其依据是星形胶质细胞内存在PTB与nPTB调节环路，该环路同样在成纤维细胞中存在，其转化原理可在多种细胞中得到验证，即较高的可重复性。另外，通过该环路进行实验分析的程序较成熟，可操作性强。 ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("实验操作")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ①本实验所需的载体、dCas系统的原理与具体通路基因搭建均有详细来源，方便实验人员构建本实验所需的基因部件 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ②验证新生多巴胺能神经元的免疫细胞化学、HPLC量化法及各项行为学检测均有详细的操作步骤与较简洁清晰的实验操作流程参考 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ③实验结果在微观层面可通过染色进行观测，宏观层面则可通过行为学测试和纹状体的解剖大体观察进行分析，在多层面上印证实验结果，使得该通路的观测的科学性得以增强 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ④经文献研究，该通路的转化效率不会过高（10%），因而难以出现实验过程中星形胶质细胞转化过多而影响其他脑区功能的现象，因而实验对象可在稳定的状态下接受各项操作 ")]),_c('div',{staticClass:"ding",attrs:{"id":"des4"}}),_c('h1',{staticClass:"topic"},[_vm._v("模型结果")]),_c('div',{staticClass:"ding"}),_c('p',{staticClass:"pagraph"},[_vm._v(" 我们的模型主要针对hsp70通路进行验证。该模型考虑了hsp70和α-syn结合以及酶促反应的动力学。模型中的参数主要来自已发表的论文以及BRENDA等数据库。根据建模结果，hsp70的过表达可以有效降低α-syn蛋白的聚集， 且能够在短时间内发生效应。 ")]),_c('div',{staticClass:"ding",attrs:{"id":"des5"}}),_c('h1',{staticClass:"topic"},[_vm._v("AAV基因治疗现存的挑战")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("抗体中和风险")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 与脑实质内（IPa）给药、鞘内（IT）或脑室内（ICV）给药不同，AAV载体的静脉内给药（IV）具有将基因非侵入性地转移至整个中枢神经系统（CNS）并且更均匀分布的潜力。迄今为止，IV给药在I型SMA婴儿中是成功的，其中非常适度水平的转基因表达可能足以获得治疗益处。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 但IV注射使AAV暴露于预先感染天然AAV的受试者，具有抗体中和的潜在风险，估计有90％的成年人接触过AAV，这可能对AAV载体应用产生深远的负面影响。目前，通过衣壳工程修饰衣壳表面表位可以更好地逃避中和抗体的识别。对于年龄较大的儿童和成人进行CNS疾病的给药，还需要考虑所需的高剂量。  ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("安全风险")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 治疗性转基因在非靶组织中的表达或在靶细胞内过表达，均有可能导致基因置换的潜在毒性作用。例如，MECP2（编码甲基-CpG结合蛋白2）中的突变导致女性的Rett综合征，但该基因的重复也导致男孩的智力发育障碍。小鼠模型中的MeCP2过表达导致行为异常、学习障碍和癫痫发作。因此，对Rett综合征的成功治疗需要转基因表达保持在非常狭窄的治疗窗内。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 与基因沉默相关的潜在毒性作用还将取决于内源蛋白的功能、干预的时间和miRNA的表达水平以及由此产生的沉默。尽管AAV基因治疗在迄今为止的一系列疾病中已被很好地耐受，但是调节表达仍然是靶标和疾病选择的重要安全考虑因素。另外，在接受基因治疗后免疫应答和转基因持久表达可能妨碍受试者参与其他临床试验。因此，具有高度未满足的临床需求和符合风险效益的疾病更加适合使用AAV基因治疗。 ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("生产成本和效率")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 用于治疗神经疾病的成功的AAV基因疗法需要一个生产平台，该生产平台可重复地提供在多个批次中具有足够和相当质量的载体，并且可用于晚期临床开发和商业化。哺乳动物细胞（如HEK293细胞）中的传统生产系统可以产生高质量载体，但产量有限。相比之下，使用杆状病毒-Sf9昆虫细胞生产AAV载体已被证明可将载体产量提高约10倍至100倍。然而，对于一些衣壳，昆虫细胞产生的载体的效力可能低于哺乳动物细胞中产生的载体，但这可以通过分子工程成功克服。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 对于需要有限量载体的CNS应用，例如脑实质内给予的AAV载体，使用哺乳动物系统可能就足够了。然而，对于静脉内（IV）和鞘内（IT）给予的AAV载体，哺乳动物系统提供临床开发所需的量都是一个重要的挑战，更不用说扩大到商业化。目前，可以扩展以满足这些需求的AAV载体生产系统包括杆状病毒-Sf9系统、基于重组HSV的系统和腺病毒-HeLa细胞系统。 ")])]),_c('div',{staticClass:"ding"}),_c('div',{staticClass:"ding"}),_c('div',{staticClass:"ding"})]),_c('div',{staticClass:"leftP",attrs:{"id":"lef"}},[_c('ul',{staticClass:"Shell"},[_c('li',[_c('a',{attrs:{"href":"#top"}},[_vm._v("Overview")])]),_c('li',[_c('a',{attrs:{"href":"#des1"}},[_vm._v("AAV应用前景")])]),_c('li',[_c('a',{attrs:{"href":"#des2"}},[_vm._v(" α-syn通路的可行性")])]),_c('li',[_c('a',{attrs:{"href":"#des3"}},[_vm._v("PTB通路的可行性")])]),_c('li',[_c('a',{attrs:{"href":"#des4"}},[_vm._v("模型结果")])]),_c('