<h1 class="title">
简介</h1>
    <p class="pagraph">
可持续发展目标（SDGs）是联合国到2030年要实现的17项目标。这些普遍目标涉及世界上紧迫的环境、政治和经济挑战，以实现所有人的更美好的未来。其中一些目标包括改善健康和教育，减少不平等和贫困，刺激经济增长以及应对气候变化。所有可持续发展目标和子目标的完整列表（https://sdgs.un.org/goals）。联合国呼吁所有国家在全球伙伴关系中帮助实现这些目标。
    </p>

    
   

<p class="pagraph">
我们项目相关的可持续发展目标有：
    </p>
     <img class="tu" src="../assets/sus2.png" alt="no pic"> 

<p class="pagraph">
3. 良好健康与福祉
    </p>

<p class="pagraph">
4. 优质教育
    </p>

<p class="pagraph">
5. 促进目标实现的伙伴关系
    </p>

<p class="pagraph">
在可持续发展目标3：良好健康与福祉方面，我们决定关注子目标3.4，该目标旨在降低“非传染性疾病和促进心理健康”的死亡率。和没有痴呆的老年人相比，患有帕金森氏症的病人的死亡风险增加了2-5倍。我们的项目旨在设计帕金森氏症多靶点治疗产品，帮助患者减轻症状，甚至痊愈，能够有效降低非传染性疾病的死亡率。同时我们关注神经疾病病人的社会关怀与心理健康状态，减少抑郁及自杀的风险。
    </p>

<p class="pagraph">
我们的项目还涉及子目标3.b，旨在开发相关药品，保护公众健康，同时保证药品价格，为更多的人提供使用药品的机会。我们和政府部门、企业合作，和医保谈判人员、药企人员交流，了解药品定价过程，为未来药品降价提供可能的思路。
    </p>

<p class="pagraph">
对于目标4，我们的项目主要针对子目标4.5，旨在消除教育中的歧视。我们的科普教育软件（和网站）中包括对神经退行性疾病的介绍、相关教育视频以及故事分享等等。通过网站和教育软件，我们希望能让更多的人们了解相关内容，让不同地区的人们都能接触到相关科普教育。我们的网站及软件具有包容性；网站及软件均具有中英文版本，并且具有语音条目，能为视障人士提供平等的教育机会。我们的网站和软件能够帮助减少教育中的歧视，因为访问网站和使用软件的权限人人平等，新冠疫情期间不论身在何处，均可访问我们的网站和软件，实现科普性教育。
    </p>
 <p class="pagraph">
我们针对目标17，和其他iGEM团队开展了系列合作。我们组织了与可持续性目标相关的会议，在会议中交流了各团队项目与可持续性目标的关联性，并发布系列微博/推文在社交媒体上进行介绍，并相互合作，帮助大家完成任务。
    </p>
     <p class="pagraph">
我们还开展了针对部分可持续性目标的队际辩论（或是和学院/学校）辩论赛事主办方联系，设定生物学伦理/道德/法律/政策/可持续性目标/人类重大健康问题等内容相关的辩题，有助于进一步了解相关内容/可持续性目标，为未来重大问题的解决提供思路。
    </p>



