        <div class="ding" id="top"></div>
    <h1 class="topic">HSP70介导的α-syn(α-synuclein)降解模型</h1>

<div class="ding"></div>
    <h1 class="title">原理简述</h1>
    <p class="pagraph">
HSP70有两条路径可以使α-syn降解。一条路径是在底物（即α-syn）结合HSP70以后，让E3泛素连接酶CHIP（carboxyl terminus of Hsc70-interacting protein）识别α-syn并将其泛素化，进而使α-syn被蛋白酶体降解；另一条路径是在α-syn结合HSP70之后，将α-syn传入溶酶体并降解。
    </p>
<div class="ding"></div>
    <h1 class="title">生物化学模型</h1>
    <p class="pagraph">
先描述共有的过程，即α-syn结合HSP70的过程。根据查找得到的动力学参数形式，将该过程描述为以下形式：
    </p>
     <img class="gong" src="../assets/m1-1.png" alt="no pic">
    <p class="pagraph">
其中，S代表底物α-syn，C代表HSP70，S·C·X（X=ADP/ATP）代表三者连接形成的复合物。上述三个反应依次记为反应1，2，3.
    </p>
    <p class="pagraph">
另外还有两个过程：蛋白质HSP70和α-syn的翻译生成过程，和HSP70的降解过程（忽略α-syn的降解，一是因为没有准确的数据难以估计，二是因为HSP70通路本身就是为了降低α-syn的含量，考虑其降解只会让α-syn减少得更快，不可能影响最终结论）。
    </p>
    <p class="pagraph">
对于生成过程，用一个常数来描述这一过程的速率。将翻译看作氨基酸作为底物、肽酰转移酶催化的酶促反应并将细胞内的氨基酸浓度视为常量，用肽酰转移酶的酶动力学参数借由米氏方程估计出速率。这里略去化学方程式，直接给出速率表达式：
    </p>
    <img class="gong" src="../assets/m1-2.png" alt="no pic">
    <p class="pagraph">
其中，v_{m,pep}=K_{cat,pep}[pep]，K_{cat,pep}为肽酰转移酶的转换数，[pep]为肽酰转移酶的浓度。作为一种较粗略的估计，HSP70和α-syn的产生速度都由这个值来表述。
    </p>
    <p class="pagraph">
对于降解过程，直接用半衰期来刻画。这里也直接给出降解的数学方程：
    </p>
    <img class="gong" src="../assets/m1-3.png" alt="no pic">
    <p class="pagraph">
其中，t_{1/2,HSP70}为HSP70的半衰期。
    </p>
    <p class="pagraph">
接下来，假设进入泛素化路径的α-syn比例为α，进入自噬途径的α-syn比例为β。进入泛素化过程的α-syn发生的反应：
    </p>
    <img class="gong" src="../assets/m1-4.png" alt="no pic">
    <p class="pagraph">
其中，S_{ubi}代表泛素化的α-syn，animoacids代表降解产生的氨基酸。
    </p>
    <p class="pagraph">
进入自噬过程的α-syn发生的反应：
    </p>
    <img class="gong" src="../assets/m1-5.png" alt="no pic">
    <p class="pagraph">
上述方程均省略了相应的酶。上述三个反应依次记为反应4，5，6.
    </p>
      <div class="ding"></div>
    <h1 class="title">数学模型</h1>
    <p class="pagraph">
根据获得的动力学参数的形式，前三个反应用结合速率常数与解离速率常数描述，后三个反应用米氏方程描述。
    </p>
    <p class="pagraph">
由此获得关于变量[S],[C·ATP],[S·C·ATP],[C·ADP],[S·C·ADP],[S_{ubi}·C·ADP]的微分方程组：
    </p>
    <img class="tu" src="../assets/m1-6.png" alt="no pic">
<div class="ding"></div>
    <h1 class="title">参数选取</h1>
    <p class="pagraph">
反应1，2，3的结合速率常数和解离速率常数摘自一篇Nature的ScientificReport（Table 1）[1]。反应4，5，6的酶动力学参数摘自酶数据库BRENDA[2]。其中，反应4由CHIP（EC2.3.2.27）催化，但由于没有找到CHIP的直接数据，其动力学参数由同属EC2.3.2的E3连接酶HECT-type E3 ubiquitin transferase（wild type，EC2.3.2.26）代替[3]。出于同样的原因和方式，反应5对应的酶用HsIU-HsIV peptidase（EC3.4.25.2）代替具有泛素依赖性的蛋白酶体26S（EC3.4.25.1）[4]。反应6的动力学参数用cathepsin H（EC3.4.22.16）估计[5]。
    </p>
    <p class="pagraph">
[amino]的值被视为一个常量，因为细胞内的氨基酸浓度不太可能发生剧烈的变化。根据[6]以及氨基酸的平均相对分子质量，可以计算出其值。
    </p>
    <p class="pagraph">
t<sub>1/2,HSP70</sub>从[7]估计。详细的参数取值见参数表。
    </p>
    <p class="pagraph">
详细的参数取值见参数表。
    </p>
    <p class="pagraph">
根据[8]，取\alpha=0.7,\beta=0.3.这一参数并不准确，所以在后续将对这两个参数进行灵敏度分析。
    </p>
    <p class="pagraph">
根据[1]中的实验条件，取初值[C·ATP]=1x10<sup>-6</sup>M,[S·C·ATP]=[C·ADP]=[S<sub>ubi</sub>·C·ADP]=[S·C·ADP]=0.而[S]的初值将以动画的形式在[0,2x10<sup>-5</sup>](M)之间变化，在讨论其他变量时维持初值3x10<sup>-6</sup>(M)。[C·ATP]的值实际上在灵敏度分析中也在一定范围内变化，但在讨论其他变量的情况下维持上述初值。
    </p>
    <div class="ding"></div>
    <h1 class="title">模型结果</h1>
    <p class="pagraph">
如下是[S]变化时，α-syn的浓度随时间的变化。
    </p>
     <img class="tu" src="../assets/m1-7.gif" alt="no pic"> 
    <p class="pagraph">
从图可知，α-syn的浓度在最开始时稍有下降，之后上升到峰值，随后持续下降，600～1000s（约合17min）后接近于零。这足以说明HSP70对α-syn的抑制作用。最开始的短暂下降是由[C·ATP]的初值造成的，这将在灵敏度分析中提到。
    </p>
  <div class="ding"></div>
    <h1 class="title">Sensitive analysis</h1>
    <p class="pagraph">
灵敏度分析的第一部分讨论(α,β)的取值带来的影响。令这两个值分别从0.1向0.9变化，得到图像如下。
    </p> 
    <img class="tu" src="../assets/m1-8.gif" alt="no pic"> 
     <p class="pagraph">
可以看到，在变化范围内，(α,β)的取值对曲线仅产生了很小幅度的影响，说明(α,β)取值不准确性的影响很小。
    </p>
     <p class="pagraph">
第二部分讨论[C·ATP]的初值变化带来的影响。令其在[0,1x10<sup>-5</sup>](M)之间变化。
    </p>    
    <img class="tu" src="../assets/m1-9.gif" alt="no pic"> 
          <p class="pagraph">
可以发现，[C·ATP]的初值主要影响最开始的下降阶段，因而影响峰值的大小和曲线的陡峭程度，但对于曲线的走势和最终的稳定值没有影响。
    </p>
      <p class="pagraph">
第三部分讨论酶浓度变化带来的影响。令所有酶的浓度在[1x10<sup>-10</sup>,1x10<sup>-6</sup>](M)之间变化。
    </p>
     <img class="tu" src="../assets/m1-10.gif" alt="no pic"> 
        <p class="pagraph">
可以发现，即使酶的浓度变化达到四个数量级，对曲线也没有产生可见的影响。所以，酶浓度估计不准确不会产生有效的影响。
    </p>


    <p class="pagraph">
参数表：
    </p>
    <img class="tu" src="../assets/m1-11.png" alt="no pic"> 
 <p class="pagraph">
