    </p>

    <div class="ding" id="des1"></div>
    <h1 class="topic">shRNA介导的SNCA基因沉默模型</h1>
     <div class="ding"></div>
    <h1 class="title">原理简述</h1>
    <p class="pagraph">
表达shRNA的载体进入细胞核，并在细胞核中不断产生shRNA。shRNA被运输到细胞质中，经过Dicer酶处理转化为siRNA。siRNA与Argonaute（Ago）结合，形成RISC；在此过程中，siRNA的一条链被舍弃，一条链被保留。被保留的单链引导RISC结合至SNCA基因转录产生的mRNA，RISC随后将mRNA降解。
    </p>
     <div class="ding"></div>
    <h1 class="title">生物化学模型</h1>
    <p class="pagraph">
参考查找得到的动力学参数的形式，将上述过程描述为以下五个小过程：
    </p>
     <img class="gong" src="../assets/m1-12.png" alt="no pic"> 
<p class="pagraph">
按排列顺序，将上述五个反应记为反应1～5.
    </p>
    <div class="ding"></div>
    <h1 class="title">数学模型</h1>
    <p class="pagraph">
反应1类似第一个模型中的做法，用RNA聚合酶的动力学参数和米氏方程估计出一个常数，用这个常速率来描述转录过程。RNA聚合酶的动力学参数来自[11]。
    </p>
    <p class="pagraph">
反应2用半衰期来描述。反应3用米氏方程来描述。Dicer的动力学参数来自[12]。
    </p>
    <p class="pagraph">
反应4中，参考已有的模型[9]，可以假设含RISC的物质总量一定，并将这些物质划分成三种状态：游离的（RISCs<sub>free</sub>），结合了siRNA的（RISC<sub>act</sub>），结合了siRNA和mRNA的（RISC<sub>bound</sub>）。但是，从获得的参考数据上看，从RISC<sub>act</sub>向RISC<sub>bound</sub>的转化非常快速彻底（正反应速率常数比逆反应速率常数大14个数量级以上）。因此，近似认为RISC<sub>act</sub>一旦生成就立刻与mRNA结合，这两种形态不再区分，统一称为RISC<sub>act</sub>。在这样的假设下，可以验证
下式恒成立，即总量一定的假设没有被破坏。
    </p>
    <img class="gong" src="../assets/m1-13.png" alt="no pic"> 
    <p class="pagraph">
反应5用米氏方程来描述。
    </p>
    <p class="pagraph">
综上，得到关于[shRNA],[siRNA],[RISC<sub>free</sub>],[RISC<sub>act</sub>],[mRNA]的微分方程组：
    </p>
<img class="tu" src="../assets/m2-1.png" alt="no pic"> 
 <p class="pagraph">
其中，v<sub>{m,X}</sub>的值与第一个模型一样，都是由转换数和酶浓度得到的。具体参数含义见参数表。
    </p>

    <div class="ding"></div>
    <h1 class="title">参数选取</h1>
    <p class="pagraph">
方程1，2中的酶动力学参数均由酶数据库BRENDA[2]查得。其中方程1所用的酶的数据来自RNA polymerase II（EC2.7.7.6），方程2所用酶的数据来自ribonuclease III（EC3.1.26.3）。
    </p>
     <p class="pagraph">
三种RNA的降解速率以及方程2，3，4涉及的两个速率常数来自[9]。其中，shRNA的降解速率数据不准确，因为结构与siRNA相似，假定其值与siRNA相同。后续进行灵敏度分析。
    </p>
     <p class="pagraph">
RISC作为酶的动力学参数来自[10]。关于该过程规律可视作与酶促反应相同的结论也来自这篇文章。
    </p>
     <p class="pagraph">
除了[RISC<sub>free</sub>]与[mRNA]，其他变量的初值均设定为0.[RISC<sub>free</sub>]的初值根据文献[9]确定。[mRNA]的初值将以动画的形式遍历，以直观证明shRNA介入的功能特性。
    </p>
     <p class="pagraph">
[poly],[Dicer],[DNA]即RNA聚合酶II、Dicer、DNA的浓度均估计为1x10<sup>-9</sup>M.后续将对这些估计值作灵敏度分析。
    </p>
     <p class="pagraph">
详细的参数值见参数表。
    </p>
     <div class="ding"></div>
    <h1 class="title">模型结果></h1>
<img class="tu" src="../assets/m2-2.gif" alt="no pic"> 
    <p class="pagraph">
上图为SNCA的mRNA初始浓度在一定范围内变化时，mRNA浓度随时间的变化曲线。可以看见，在这一范围内，无论mRNA的初始浓度为多少，在经过6000～10000s（约合3h）之后，mRNA水平都会被控制到一个不随mRNA浓度初值变化而变化的常量。这说明shRNA通路对mRNA水平有较好的控制能力。并且，从灵敏度分析（见下）的第一部分可以知道，通过调控转录的速率可以做到控制这一常量的大小。这说明通过线路元件的合理设计，将SNCA控制在希望的某一水平是可行的。
    </p>
     <div class="ding"></div>
    <h1 class="title">Sensitive analysis</h1>
    <p class="pagraph">
灵敏度分析的第一部分将阐明转录速度如何影响模型结果。
    </p>
     <p class="pagraph">
如上所述，转录的速率是自行计算得出的，这一速率的值是4.59x10<sup>-11</sup>M/s.现在改变这一数值，使其在[0,1x10<sup>-10</sup>](M/s)之间变化，得到的效果如下。（此时mRNA浓度初值设为0）
    </p>
    <img class="tu" src="../assets/m2-3.gif" alt="no pic"> 
     <p class="pagraph">
可以看到，随着转录速度的增长，曲线的大致形状没有改变，但是最后到达的稳定水平改变了。这就说明了调控转录速度能够控制最终的mRNA水平。
    </p>
     <p class="pagraph">
第二部分证明在参数中，准确度比较低的三个值[poly],[Dicer],k<sub>shRNA</sub>对模型的影响极小，因而参数的不准确不会有影响。（[DNA]之所以不讨论，是因为其被估计出来是为了估算转录速度，而转录速度的影响已经在上一部分讨论过。参数本身并没有太大意义）
    </p>
     <p class="pagraph">
先讨论[poly],[Dicer]。同时改变[poly],[Dicer]的值，使其在[1x10<sup>-10</sup>,1x10<sup>-8</sup>](M)之间变化。结果如下。
    </p>
     <img class="tu" src="../assets/m2-4.gif" alt="no pic"> 
     <p class="pagraph">
从结果中可以看到，这两个值在变化的时，曲线并没有发生肉眼可见的变化。换言之，这两个参数的不准确性不会对结果造成很大影响。
    </p>
     <p class="pagraph">
再讨论k<sub>shRNA</sub>}。令其在[1x10<sup>-6</sup>,1x10<sup>-2</sup>](1/s)内变化，得到图像。同理，可以看出该参数的不准确性不会带来可见的影响。
    </p>
    <img class="tu" src="../assets/m2-5.gif" alt="no pic"> 
