AN C, STEWART T, et al. Development of a Sensitive Diagnostic Assay for Parkinson Disease Quantifying alpha-Synuclein-Containing Extracellular Vesicles[J]. NEUROLOGY, 2021,96(18): E2332-E2345. ")]),_c('p',{staticClass:"ref"},[_vm._v(" [4] HERRMANN Y, BUJNICKI T, ZAFIU C, et al. Nanoparticle standards for immuno-based quantitation of alpha-synuclein oligomers in diagnostics of Parkinson's disease and other synucleinopathies[J]. CLINICA CHIMICA ACTA, 2017,466: 152-159. ")]),_c('div',{staticClass:"ding"}),_c('div',{staticClass:"ding"}),_c('div',{staticClass:"ding"}),_c('div',{staticClass:"ding"})])]),_vm._m(4)])}
