        <h1 class="topic">公众教育与同伴教育</h1>
        <h1 class="title">校内开展的TheraParkin介绍会</h1>
        <p class="pagraph">
          我们通过浙江大学医学院职规中心邀请浙大系附属医院的神经内/外科医生，通过辅导员以及医学院团委开展宣传，以科室介绍的方式吸引校内医学生团体的参与。另外，我们开展了与生科院、农科院相关社团的合作，通过联合宣传，我们吸引了较为庞大的观众群体。我们拟邀请刘畅学姐介绍iGEM与她的八年医学生涯，分享介绍合成生物学与临床科研工作生活。
        </p>
        <img class="tu" src="../assets/edu1-1.png" alt="no pic" />
        <p class="pagraph">
