                iGEM队员通过暑期支教机会，向支教地的同学们介绍合成生物学、帕金森病与TheraParkin。我们设计乐高积块向同学们介绍Biobrick与合成生物学的核心思想。通过信息技术课程，我们将合成生物学思想与编程思想融合，设计相应生物模块，融入帕金森病元素。利用TheraParkin的两条治疗模块，构建治疗线路模块，引导同学们利用TheraParkin元件实现帕金森病的治疗。我们希望以此成为同学们编程思想的启蒙课，同时也让他们感受到合成生物学模块化、标准化的思想。
              </p>
              <p class="pagraph">
                除此之外团队成员拟联系杭州支教服务团队---醒山薪火联盟，去往杭州郊区学校调研，向同学们介绍合成生物学与高中生物学的异同，向其普及帕金森病以及TheraParkin项目，并收集高中段同学的对项目的疑问，留待团队进一步改进。
              </p>
            </div>
            <div id="p2" class="p">
              <p class="pagraph">
