            </p>
            <p class="pagraph">
              纪录片的内容更多聚焦帕金森患者运动功能的紊乱所造成的生活不便，以及患者和家属生理心理问题。我们拟参考美国关于COPD患者的纪录片，以对比的手法开展默片式宣传；也将适当参考《凉子访谈录》的拍摄手法，以跟拍与访谈的形式作为纪录片的基调，以日常场景的搭建与还原作为纪录片的主体。
            </p>
            <p class="pagraph">
