        <h1 class="title">Abstract</h1>
        <p class="pagraph">
          Parkinson’s disease (PD) is a neurodegenerative disorder that affects
          a significant portion of elderly populations in the world. Current
          therape utic approaches include surgery, medication, and supportive
          therapies. Howeve r, these therapeutic approaches are not useful for
          everyone, and they also have side effects. Therefore, we decided to
          develop a multi-target therapy t o treat PD. Our therapy is based on
          two different adeno-associated virus (AA V) vectors containing
          different plasmids. For the first vector, we applied a n AND gate
          minigene circuit to achieve SNCA gene silencing and Hsc70 overexpr
          ession, specifically in dopaminergic (DA) neurons. This vector can
          reduce the toxic α-synuclein aggregates in DA neurons, and therefore
          relieve the PD sympto ms. For another vector, we designed a PTB gene
          silencing circuit based on the d Cas9-DNMT3A DNA methylation system.
          This vector will target astrocytes in the brain, effectively inducing
          these cells to convert to functional neurons. In ou r project, we also
          designed tissue-specific promoters, suicide switches, and sp ecific
          virus capsid to ensure safety in clinical practice. Both vectors we
          desi gned can be used to treat patients under different pathogenesis,
          which may provi de clues for a potential cure for PD in the future.
        </p>

        <div id="des1" class="ding"></div>
        <h1 class="title2">
          帕金森病简介<a class="top" href="#top"
            ><img src="../assets/top.png" alt="top"
          /></a>
        </h1>
        <p class="pagraph">
          帕金森病(Parkinson’s disease, PD)是一种好发于中老年神经系统的变性疾病,
          是继阿尔茨海默病之后的第二大常见神经退行性疾病。其病理特征包括黑质纹状体多巴胺能通路的进行性退化、黑质致密部神经元的大量丢失以及多巴胺的耗竭。PD的临床症状主要表现为静止性震颤、肢体僵硬、运动迟缓和缺乏自主性运动。大量证据表明,
          由于运动性的退化,
          PD患者继而出现如自主功能不全、认知缺损、抑郁、嗅觉丧失、精神和睡眠障碍等非运动特征的并发症
        </p>
        <p class="pagraph">
          世界PD协会资料和流行病学调查显示, 全球已有500多万PD患者。其中,
          55岁以上人群中PD患病率为1.4%, 75岁以上人群中患病率为3.4%,
          85岁以上人群中患病率可4.0%。自20世纪90年代末以来，尽管生物技术和医学取得了进步，但死亡率已经翻了一番。随着人口平均年龄的逐年增长，帕金森氏症和其他神经退行性疾病的威胁只会加剧。但目前为止，帕金森氏症的治疗只能减轻症状和减缓疾病的进展，但并不能阻止神经变性，更无法治愈。
        </p>
        <div id="des2" class="ding"></div>
        <h1 class="title2">
          现有治疗方式<a class="top" href="#top"
            ><img src="../assets/top.png" alt="top"
          /></a>
        </h1>
        <p class="pagraph">
          当前帕金森病的传统方法包括药物、手术和基因疗法。选择以左旋多巴为代表的多巴胺能药物及抗胆碱能药物为目前药物治疗的主要手段，其缺点为人体逐渐对药物建立耐受，无法实现可持续的治疗效果，因此药物治疗不是最理想的治疗途径。帕金森的手术治疗主要依靠脑深部刺激术，虽然副作用较小但价格昂贵，因而并不适合于所有的帕金森病人。通过进一步研究调查，我们把目光转向了逐渐走入人们视野的基因疗法。
        </p>
        <p class="pagraph">
          腺相关病毒（adeno-associated
          virus，AAV）是一种单链DNA病毒，隶属细小病毒科。这是一类无法自主复制、无被膜的二十面体病毒，直径很小，约为20nm。野生型的AAV基因组大小为5kb左右，包含两侧存在反向末端重复序列（inverted
          terminal
          repeats，ITRs）的rep基因和cap基因。前者主要控制病毒基因组复制和病毒颗粒组装，而后者控制的则是其衣壳蛋白的合成与组装。
        </p>
        <p class="pagraph">
          在基因治疗中使用的AAV载体是经过人工改造的重组AAV（rAAV），其只保留了145bp的ITRs序列，这也意味着其余约96%的基因组都可以被基因治疗需要的基因序列替换，使其拥有非常强大的包装能力。除此之外，AAV还有种类繁多、免疫原性极低、安全性高、宿主细胞范围广、扩散感染能力强、基因表达时间长等优点。正是这些优点，使得AAV被作为一个常用的载体用于基因治疗中。目前，基于AAV载体的多项眼病、血友病以及肌肉疾病治疗方案已经进入临床试验阶段，这也充分证明了使用AAV作为载体的基因治疗的安全性和有效性。
        </p>
        <p class="pagraph">
          在帕金森病的基因治疗中，当前热点的目的基因主要有影响多巴胺代谢的相关基因、神经营养因子基因、神经递质平衡调节相关基因等。
        </p>
        <p class="pagraph">
          代谢通路的关键基因包括酪氨酸羟化酶（tyrosine
          Hydroxylase,TH）、GTP环化水解酶1（GTP cyclization hydrolase
          1,GCH-1）等。通过导入此类基因增加多巴胺含量以治疗帕金森症状。
        </p>
        <p class="pagraph">
          神经营养因子基因如GDNF、BDNF等可通过特异性保护多巴胺能神经元来实现多巴胺的正常表达，通过导入此类基因也是帕金森病治疗的常用策略之一。
        </p>

        <div id="des3" class="ding"></div>
        <h1 class="title2">
          项目设计简介<a class="top" href="#top"
            ><img src="../assets/top.png" alt="top"
          /></a>
        </h1>
        <p class="pagraph">
          此次我们关注到了代谢通路中的α-syn通路治疗以及神经元再生的PTB通路，通过AAV载体，在代谢与细胞再生两个层面实现帕金森病的长效稳定治疗。
        </p>
        <p class="pagraph">
          我们的项目开发了两种不同的腺相关病毒载体，分别针对不同的致病机理，旨在进行帕金森疾病的多靶点治疗。我们选用了2022年新开发的AAV-CAP-B10腺相关病毒载体，它能够通过静脉注射进入人体，穿过血脑屏障，对中枢神经系统具有高度特异性，且对肝脏具有低靶向性。我们同时修饰了病毒衣壳，增加其对目标组织的靶向性。
        </p>
        <p class="pagraph">
          在我们的设计中，其中一种病毒载体是靶向中脑黑质多巴胺能神经元的载体，我们选用了Pitx3、Foxa2特异性启动子并构建了与门电路，两个启动子在细胞中同时启动时，我们设计的基因线路才会表达。基因线路是基于CRISPReader的微型基因环路，当xxx,
          xxx启动子同时启动时，shRNA表达，特异性沉默SNCA基因，减少α-syn蛋白的产生，从而减少α-syn蛋白不正常的聚集；同时病毒质粒上含有的Hsp70基因将会被表达，通过分子伴侣调控的细胞自噬途径降解α-syn蛋白聚集体，恢复被α-syn蛋白聚集体毒性阻断的细胞信号通路，减轻帕金森疾病的症状。
        </p>
        <p class="pagraph">
          另一种病毒载体靶向脑内的星形胶质细胞，我们选用了GFAP星形胶质细胞特异性启动子增强靶向性。我们基于dCas9-DNMT3A系统，实现PTB基因的甲基化修饰，以沉默PTB基因。PTB基因沉默后，部分星形胶质细胞被诱导转化为多巴胺能神经元，重建帕金森病人脑中中断的中脑-多巴胺能神经元回路，恢复病人的运动功能，是一种稳定长效的治疗方法。
        </p>
