var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('div',{staticClass:"bg"},[_c('div',{staticClass:"im",attrs:{"id":"bg"}})]),_c('div',{staticClass:"back"},[_c('div',{staticClass:"content"},[_c('div',{staticClass:"ding",attrs:{"id":"top"}}),_c('p',{staticClass:"pagraph"},[_vm._v(" 我们的项目 TheraParkin 意在解决神经退行性疾病帕金森的治疗问题。TheraParkin作为一种药物，需要充分保证安全性和有效性，所以我们在病毒基因层面作出了多项安全保障设计，具体详见“项目安全设计”模块。另外，我们考虑到设计最终会用于临床实施，需要兼顾实施过程中的安全问题，这将在我们的“实施安全设计”部分进行讨论。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 我们考虑到，TheraParkin在使用过程中可能对人体环境造成危害，我们针对项目具体讨论了这一情况的可能性，这将在“人体、环境安全”显示。同时，实验过程中保证规范的生物安全也非常重要，在“生物安全”部分我们将展示我们为保证生物安全在两用与实验室安全方面做出的努力。 ")]),_c('div',{staticClass:"ding",attrs:{"id":"des1"}}),_c('h1',{staticClass:"topic"},[_vm._v(" 项目安全设计")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("底盘生物与病毒衣壳")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 我们选择了腺相关病毒（AAV）作为底盘生物，它是目前免疫治疗和基因治疗研究中广泛使用的。腺相关病毒是RG1（最安全级别）的基因治疗载体，基本不具有潜在致病性，不能自我复制。现行AAV相关疗法中，目前已有3个基于AAV的基因治疗产品获批上市，上百个在研疗法已进入临床开发阶段，安全性具有一定保障。根据项目需求，我们选用了两种载体——AAV-CAP-B10和rAAV6。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 我们在载体的基础上对其外壳蛋白进行设计，以便具有更高的组织特异性。使用脑组织特异性的AAV衣壳蛋白AAV CAP-B10，能够穿透血脑屏障，特异性表达于中脑多巴胺能神经元；rAAV6具有穿越血脑屏障，高特异性靶向星形胶质细胞的能力，通过外壳蛋白设计，可以实现脑组织特异性。 ")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/sa2-1.png"),"alt":"no pic"}}),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/sa2-2.png"),"alt":"no pic"}}),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("特异性启动子与转录因子")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 为了减少AAV侵染过程中带来的风险泄露问题，我们对两条线路都有独特的特异性转录因子或启动子设计。在α-syn蛋白通路中，我们选择了两个特异性表达在多巴胺能神经元中的转录因子PITX3和FOXA2，来驱动“与门”线路的表达；在PTB途径中，我们选择了星形胶质细胞的特异性启动子GFAP。这两类因子对于靶细胞和非靶细胞来说有足够的特异性，从而使得基因的表达量维持一个安全的状态。 ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("给药方式")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 我们的项目采用静脉注射（IV）的方式进行给药。迄今为止的研究表明AAV载体的静脉注射方式具有将基因非侵入性地转移并且更均匀分布的潜力，适度水平的转基因表达具有治疗方面的益处。但是，即使AAV已经具有较低的免疫原性，由于IV注射会使AAV暴露于预先感染天然AAV的受试者，AAV仍具有抗体中和的潜在风险。基于此，我们在腺相关病毒基因组中插入TLR9i（短单链DNA寡核苷酸序列），对AAV实现“伪装”作用，从而更好地逃避中和抗体的识别，降低免疫原性，提高项目的安全性。 ")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/sa2-3.png"),"alt":"no pic"}}),_c('div',{staticClass:"ding",attrs:{"id":"des2"}}),_c('h1',{staticClass:"topic"},[_vm._v("实施安全设计")]),_c('img',{staticClass:"tu",attrs:{"src":require("../assets/sa3-1.png"),"alt":"no pic"}}),_c('p',{staticClass:"pagraph"},[_vm._v(" 制造：优化生产工艺，获得高纯度载体 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 运输：使用冰袋运载，减少泄露风险 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 使用：接触、信息风险、滥用… ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 处理：使用完的产品残留的正确回收，特定回收处与回收方法 ")]),_c('div',{staticClass:"ding",attrs:{"id":"des3"}}),_c('h1',{staticClass:"topic"},[_vm._v("人体、环境安全")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("基因毒性")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 腺相关病毒(AAV)作为基因治疗载体，在人体中具有低遗传毒性特征，AAV基因组主要以附加体的形式存在于宿主细胞核，基本不引起载体基因组介导的宿主遗传毒性。由于AAV在临床试验中的安全记录和在该领域内的共识，很少有工作专门针对限制基因毒性整合事件的策略。尽管如此，我们也可以通过调节控制盒限制附近基因反式激活的绝缘子元件的设计来减少由于杂散整合引起的遗传毒性效应。 ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("载体毒性")]),_c('p',{staticClass:"pagraph"},[_vm._v(" AAV作为基因载体，首先考虑自身的毒力恢复问题，由于AAV本身并不具有致病性，故在人体环境中较为安全。AAV 病毒的基因组是单链 DNA，在体外环境形成双链并转录翻译外源基因的效率很低，一般采用同时感染辅助病毒（如 Ad5 型腺病毒）或者终浓度 10~50 mM 的丁酸钠等方法提高细胞实验的 AAV 表达量。若采用前者方式提高表达量，需要考虑腺病毒等辅助病毒对人体和环境的风险，这里可以参考iGEM 2021 ZJU-China 团队关于腺病毒的安全讨论。 ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("肝脏毒性")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 有研究曾指出，AAV载体给药可能会积累肝脏毒性，原因在于原AAV设计靶向性不高，需要高剂量给药才能达到既定药效。我们的设计从底盘、衣壳设计和启动子设计等方面增强AAV靶向特异性，在低剂量范围内即可呈现效果，能够有效地降低肝脏毒性，保证人体环境的安全。 ")]),_c('div',{staticClass:"ding",attrs:{"id":"des4"}}),_c('h1',{staticClass:"topic"},[_vm._v("生物安全")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("两用性")]),_c('p',{staticClass:"pagraph"},[_vm._v(" iGEM 重视安全和生物安全，并明确指出团队应管理任何风险。 在这些风险中，iGEM 建议可以通过双重使用实践来管理误用风险。即使是低毒性的设计，也应保证生物技术不能被滥用。在生物安全领域，我们承诺遵守中国现行的相关法律法规。 ")]),_c('p',{staticClass:"ref"},[_vm._v(" 中华人民共和国生物安全法（http://www.gov.cn/xinwen/2020-10/18/content_5552108.htm） ")]),_c('p',{staticClass:"ref"},[_vm._v(" 中华人民共和国药品管理法实施条例（http://www.gov.cn/zhengce/2020-12/26/content_5573533.htm） ")]),_c('p',{staticClass:"ref"},[_vm._v(" 病原微生物实验室生物安全管理规定（http://www.gov.cn/zhengce/2020-12/27/content_5574545.htm） ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 作为一个负责任的团队，我们考虑到如下可能发生生物安全滥用的风险条目：第一，关于AAV载体的剂量问题；第二，环境释放腺相关病毒问题；第三，知识和数据库滥用问题。 ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("实验室安全")]),_c('h3',{staticClass:"secTitle"},[_vm._v("基本安全")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 根据iGEM比赛的需求，团队成员学习了实验室安全培训，在被允许在实验室进行任何实验之前，完成涵盖所有相关活动、化学品、生物体和其他相关安全信息的一般安全报告的核验工作， 所有获得的生物部分（基因、质粒、菌株）首先在该机构的本地存储库中注册，并在订购前获得批准。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 实验室配备应急洒水器、洗眼器、通风柜等设备，确保我们的安全。 实验室还拥有完善的安全应急设施，满足生物安全一级实验室的安全防护要求。 ")]),_c('div',{staticClass:"ding"}),_c('h3',{staticClass:"secTitle"},[_vm._v("BSL-1")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 我们将在生物安全一级实验室进行实验。我们使用的底盘生物AAV-CAP-B10和rAAV6属于BSL-1，经过安全研究，确认其不会对人类健康或环境造成严重的威胁。在实验室进行工作时，始终有主管或指导员在场。 ")]),_c('div',{staticClass:"ding"}),_c('h3',{staticClass:"secTitle"},[_vm._v("COVID-19")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 为了确保我们团队成员在 COVID-19 方面的安全，我们采取了以下措施： "),_c('ul',[_c('li',[_vm._v("团队成员定时核酸检测，在出现与 COVID-19 相关的症状时及时接受隔离处理。")]),_c('li',[_vm._v("遵循多项卫生规则和清洁措施，例如经常清洁手部、工作场所和厕所。")]),_c('li',[_vm._v("团队成员和其他进入大楼的人员在从一个地方转移到另一个地方时必须戴上口罩。")]),_c('li',[_vm._v("实验室只允许同时有少量学生在场，我们制定了实验时间表，以清楚地显示实验人员的排班情况。 ")])])])]),_c('div',{staticClass:"ding"}),_c('div',{staticClass:"ding"}),_c('div',{staticClass:"ding"})]),_c('div',{staticClass:"leftP",attrs:{"id":"lef"}},[_c('ul',{staticClass:"Shell"},[_c('li',[_c('a',{attrs:{"href":"#top"}},[_vm._v(" Top")])]),_c('li',[_c('a',{attrs:{"href":"#des1"}},[_vm._v("项目安全设计")])]),_c('li',[_c('a',{attrs:{"href":"#des2"}},[_vm._v("实施安全设计")])]),_c('li',[_c('a',{attrs:{"href":"#des3"}},