        <div id="top" class="ding"></div>
        <p class="pagraph">
          我们的设计实现的目标有两个，一是通过降低α-syn蛋白的表达和诱导自噬，减少由于α-syn的聚集对黑质多巴胺能神经元造成的毒性；二是通过诱导神经胶质细胞转化为多巴胺能神经元，补充死亡的神经元。
        </p>
       <p class="pagraph">
从实验上具体来说，我们构建了如下parts：

    </p>
    <p class="pagraph">
<ul>
  <li>人类野生型SNCA基因的编码序列
</li>
  <li>人类突变型SNCA基因的编码序列
</li>
  <li>人类Hsp70基因的编码序列
</li>
  <li>靶向沉默SNCA基因的shRNA序列
</li>
  <li>基于CRISPReader的与门逻辑电路
</li>
  <li>使用甲基化沉默PTB基因表达的dCas9系统
</li>
</ul>
    </p>


        <div id="des1" class="ding"></div>
        <h1 class="topic">初步实验设计</h1>
        <div class="ding"></div>
        <h1 class="title">α-syn通路实验设计</h1>
        <p class="pagraph">
            <ol>
                <li>
                    分别将Hsp70过表达质粒、野生型SNCA:EGFP融合蛋白质粒、突变型SNCA:EGFP融合蛋白质粒转染进VERO细胞系中，并培养一段时间。</li>
                <li>
                    ②WB验证Hsp70过表达成功，并观察野生型α-syn蛋白的细胞定位和聚集情况以及突变型α-syn蛋白的细胞定位和聚集情况。
                </li>
                <li>
                    ③在表达有突变型α-syn蛋白的细胞系中分别过表达Hsp70和SNCA shRNA，与对照对比观察突变型α-syn蛋白的分布情况和聚集情况。同时可以在过表达Hsp70的细胞中进行自噬信号的检测，验证Hsp70能提高α-syn的自噬。
                </li>
                <li>
                    ④以AAV-CAP-B10为底盘生物设计最终的质粒，并包装病毒载体。在不同种类的细胞中观察其侵染效率和侵染情况，验证特异性（有条件的话这一步最好使用合适的报告基因作为最终的效应基因以较好的量化差异，但是会导致要多包装一次病毒）和最终的治疗效果。同时检测AAV侵染后神经细胞的凋亡信号，验证侵染后不会对神经元的活动造成损伤。
                </li>
            </ol>
        </p>
        
        <div class="ding"></div>
        <h1 class="title">PTB通路实验设计</h1>
        <p class="pagraph">
         <ol>
             <li>
                 将dCas9工具导入体外培养星形胶质细胞中，通过RT-qPCR验证PTB的沉默。
             </li>
             <li>
                 将hU6-sgRNA-GFAP-dCas-DNMT3A-WPRE质粒包装成病毒，侵染星形胶质细胞，观察星形胶质细胞
             </li>
             <li>
                 计算被诱导分化为多巴胺能神经元的概率以及分化出的神经元的电生理等一系列生理特性。
             </li>
             <li>
                 免疫细胞化学
             </li>
         </ol>
        </p> 
        <p class="pagraph">
         在载玻片上生长的培养细胞在室温下用 4% 多聚甲醛（PFA，Affymetrix）固定 15 分钟，然后在冰上用 PBS 中的 0.1% Triton X-100 渗透 15 分钟。用 PBS 洗涤两次后，在室温下将细胞在含有 3% BSA 的 PBS 中封闭 1 小时。固定细胞与一抗一起孵育。 在 4 °C 下在含有 3% BSA 的 PBS 中过夜。用 PBS 洗涤两次后，将细胞与与 Alexa Fluor 488、Alexa Fluor 546、Alexa Fluor 594 或 Alexa Fluor 647（1:500，Molecular Probes）缀合的二抗孵育 1 小时。在室温下将 DAPI（300 nM 在 PBS 中）应用于细胞 20 分钟以标记细胞核。用 PBS 清洗 3 次后，将 Fluoromount-G 封固剂涂在载玻片上，并在 Olympus FluoView FV1000 下检查和记录图像。
        </p> 
<p class="pagraph">
         预期结果：经过免疫染色，发现中脑来源的星形胶质细胞产生TH+神经元的比例较其他脑区更高（5倍），且具有一定的转化效率（10%）
        </p>  



        <div id="des2" class="ding"></div>
         <h1 class="topic">实验流程</h1>
        <p class="pagraph">
         <ol>
             <li>
                 培养星形胶质细胞、多巴胺能神经元（可能不太能实现？不知道能不能构建有α-syn聚集的多巴胺能神经元模型）
             </li>
             <li>
                 构建质粒（SNCA shRNA, Hsp70基因,hU6-sgRNA-GFAP-dCas-DNMT3A-WPRE,W-SCNA:GFP,m-SCNA:GFP）
             </li>
             <li>
                 验证质粒成功构建（琼脂糖凝胶电泳；转化进大肠杆菌，摇菌+送公司测序）
             </li>
             <li>提取质粒，转化进细胞，培养一段时间</li>
             <li>qRT-PCR检测SNCA mRNA的量（转染了SNCA shRNA质粒/转染空质粒 对照）
质粒转染神经元/星胶/其他细胞，验证启动子特异性（表达水平差异）</li>
         </ol>
        </p> 
      </div>
