var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_vm._m(0),_c('div',{staticClass:"back"},[_c('div',{staticClass:"content"},[_c('div',{staticClass:"ding",attrs:{"id":"top"}}),_c('h1',{staticClass:"topic"},[_vm._v("概述")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 今年，ZJU-China团队开发了对抗帕金森疾病的一款药物——TheraParkin，在整个研究周期中，我们充分关注了项目的安全性和可行性，并做出了出色的实验设计和验证。然而，如何让这些实验成果走向现实社会，是我们需要重视的问题。为此，我们站在企业家的角度进行了全方位的创业考虑，并对未来发展作出了规划。 ")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 作为一家由大学生创办的未来创业型医药企业，我们在实践过程中不断向各类主体吸收意见。我们遵循负责任、人性化的原则，制定了药物产品的商业计划和专利策略，期望达到推广产品，保障健康的目标。另外，我们与各大行业机构建立了广泛的商业合作，并积极参与创新创业类活动。根据可持续性原则，我们建立了专项的创业学习资源分享平台，希望这些资源能够为之后团队所用。 ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("帕金森创新药市场环境")]),_c('p',{staticClass:"pagraph"},[_vm._v(" TheraParkin是一款抗帕金森病的药物，隶属于医药行业中的创新药领域。我们注意到这款由我们自主研发的药物在创新药市场中具有极大的推广潜力。下面我们将借助PEST模型来对帕金森创新药市场环境进行评估。 ")]),_c('div',{staticClass:"showBox"},[_c('div',{staticClass:"head"},[_c('div',{staticClass:"c",attrs:{"id":"c1"},on:{"click":function($event){return _vm.ch1()}}},[_vm._v("政策")]),_c('div',{staticClass:"c",attrs:{"id":"c2"},on:{"click":function($event){return _vm.ch2()}}},[_vm._v("经济")]),_c('div',{staticClass:"c",attrs:{"id":"c3"},on:{"click":function($event){return _vm.ch3()}}},[_vm._v("社会")]),_c('div',{staticClass:"c",attrs:{"id":"c4"},on:{"click":function($event){return _vm.ch4()}}},[_vm._v("技术")])]),_vm._m(1)]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("ZJU-TheraParkin公司在市场中的定位")]),_c('p',{staticClass:"pagraph"},[_vm._v(" ZJU-TheraParkin公司致力于帕金森中期、晚期的药物研发工作，并在早期提供检测服务（详情可见硬件部分）。我们根据实际情况选择了“VIC“的研发模式，并明确了公司在产业链中的定位。 ")]),_c('div',{staticClass:"ding"}),_c('h1',{staticClass:"title"},[_vm._v("研发模式：“VIC”")]),_c('p',{staticClass:"pagraph"},[_vm._v(" 目前现有的制药公司研制创新药的研发模式主要有三种形式：大而强的Bigpharma模式、小而美的Biotech模式和新颖的“VIC”模式。作为以实验研究成果为核心技术的初创公司，我们不具有Bigpharma模式所属的重资产体系，也没有Biotech企业需要的首席科学家坐镇，因此，我们选择了第三种模式——“VIC模式”。 ")]),_c('p',{staticClass:"pagra